<template>
  <div class="navigation-circle-div">
    <span class="half-circle btn-menu-under">
      <button class="half-circle btn-menu" type="button" name="button" @click="return_home">
        <span></span>
      </button>
    </span>
  </div>
</template>

<script>

export default {
  data(){
    return{
      show: false
    }
  },
  methods:{
    return_home:function(){
      this.$emit("input", "home");
    }
  },
  computed:{
    image_src:function(){
      var images = require.context('./../assets/', false)
      return images("./bunny-small.svg")
    },
  }
}
</script>
