<template>
    <div class="home">
      <Code v-model='value' @setView='setView'></Code>
      <div class="welcome-msg">
        <p  v-if='value == PAGE.HOME'>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Morbi volutpat ultrices lacinia. Integer sed vulputate enim. </p>
      </div>
      <transition name="rotate-code">
        <div class="libra-div" v-if='value != PAGE.CODE'>
        <transition name="home-about">
          <div class="wrapper" v-if='value != PAGE.ABOUT'>
            <button class="libra-btn" @click='setView(PAGE.ABOUT)' tabindex='-1'></button>
            <div class="libra-buttons-div">
              <button type="button" class="btn-code" name="button" @click='setView(PAGE.CODE)' tabindex='-1'>CODE</button>
              <button type="button" class="btn-art" name="button" @click="setView(PAGE.ART)" tabindex='-1'>ART</button>
            </div>
          </div>
        </transition>
        </div>
      </transition>
    </div>
</template>

<script>
import Page from './../mixins/Page.vue';
import Code from './Code.vue';

export default {
  mixins:[Page],
  props:['value'],
  name: 'Home',
  data(){
    return{
      vue_canvas: null,
      padding: 0,
      animation:""
    }
  },
  computed:{
    clickable:function(){
      return (this.value != this.PAGE.HOME);
    }
  },
  methods:{
    setAnimation:function(val){
      switch (val) {
        case this.PAGE.ABOUT:
          this.animation = "home-about";
          break;
      }
    },
    setView:function(view){
      this.setAnimation(view);
      this.$emit("setView", view);
    },
  },
  mounted:function(){},
  components: {
    Code
  }
}
</script>
