<template>
  <div id="about">
    <div class="about-wrapper">
      <div class="about-image-div">
        <button class="btn-home" type="button" name="button" @click='home()' tabindex='-1'>
          <img :src="image_src" alt="">
        </button>
      </div>
      <div class='desc-div'>
        <div class="desc-panel-div">
          <div class="about-header">
            Who Am I?
          </div>
          <div class="desc">
            Aileen Zhou, an artist from Brooklyn, specializes in digital media and digital illustrations. Prevalent in her work are use of bold silhouettes and colors, as well as women and fantasy figures in repose or in contemplation.
          </div>
        </div>
        <div class="media column">
          <button type="button" name="button" @click='openEmail()'><span>E-MAIL</span></button>
          <button type="button" name="button" @click='goToTwitter()'><span>TWITTER</span></button>
          <button type="button" name="button" @click='goToInstagram()'><span>INSTAGRAM</span></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props:["value"],
  data(){
    return{}
  },
  computed:{
    image_border_src:function(){
      var images = require.context('./../assets/', false)
      return images("./border-circle.svg")
    },
    image_src:function(){
      var images = require.context('./../assets/', false)
      return images("./libra-stand.svg")
    },
    star_scale:function(){
      return (window.innerWidth < 860) ? 1.0 : 0.36;
    }
  },
  methods:{
    home:function(){
      this.$emit("input", "home");
    },
    openEmail(){
      window.location.href = "mailto:ayleinee@gmail.com";
    },
    goToTwitter(){
      window.open("https://twitter.com/ayleinee/");
    },
    goToInstagram(){
      window.open("https://instagram.com/ayleinee/");
    }
  },
  mounted:function(){

  },
  components:{
  }
}
</script>
